.page-header__nav {
  position: relative;
  margin: 0 auto;

  @media (max-width: $mobile-width) {
    max-width: 500px;
  }

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 30px 17px;
  }

  @media (min-width: $desktop-width) {
    width: 1220px;
    padding: 53px 0 29px;

    &::before {
      display: block;
      position: absolute;
      right: 0;
      top: 0;

      width: 720px;
      height: 688px;
      background-color: #7ec157;
      background-image: url("../img/background/promo-bg-desktop.jpg");
      content: "";
    }
  }

  &--no-bg {
    @media (min-width: $desktop-width) {
      &::before {
        display: none;
      }
    }
  }
}

.page-header__wrap {
  display: grid;
  grid-template-columns: 1fr 24px;
  align-items: center;
  padding: 10px 20px 17px;
}

.page-header__logo {
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
  text-align: center;
}

.logo__name {
  justify-self: center;
  width: 101px;
  height: 18px;
}

.page-header__toggle {
  position: relative;
  width: 24px;
  height: 18px;
  padding: 0;
  background-color: $white;
  border: none;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.page-header__nav--closed .page-header__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: $black;
  box-shadow:
    0 8px $black,
    0 16px $black;
}

.page-header__nav--opened .page-header__toggle {
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 8px;
    left: 0;
    width: 24px;
    height: 1.5px;
    background-color: $black;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}
